.loginScreen {
  position: relative;
  height: 100%;
  background: url("https://cdn.wallpapersafari.com/24/74/zgeTuV.jpg") center
    no-repeat;
  background-size: cover;
}

.loginScreen_logo {
  position: fixed;
  left: 0;
  width: 150px;
  object-fit: contain;
  padding-left: 20px;
  padding-top: 20px;
}

.loginScreen_button {
  position: fixed;
  right: 20px;
  top: 20px;
  padding: 8px 20px;
  color: #fff;
  border-radius: 4px;
  font-size: 1rem;
  background-color: #e50914;
  border: none;
  cursor: pointer;
}

.loginScreen_gradient {
  width: 100%;
  z-index: 1;
  height: 100vh;
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.8) 0,
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, 0.8) 100%
  );
}

.loginScreen_body {
  position: absolute;
  text-align: center;
  top: 30%;
  z-index: 1;
  color: #fff;
  padding: 20px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
}

.loginScreen_body > h1 {
  font-size: 3.125rem;
  margin-bottom: 20px;
}

.loginScreen_body > h2 {
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 30px;
}

.loginScreen_body > h3 {
  font-size: 1.3rem;
  font-weight: 400;
}
.loginScreen_input > form > input {
  padding: 10px;
  outline-width: 0;
  height: 30px;
  width: 30%;
  border: none;
  max-width: 600px;
  margin-right: 10px;
  border-radius: 4px;
}

.loginScreen_input {
  margin: 20px;
}
.loginScreen_getStarted {
  padding: 16px 20px;
  font-size: 1rem;
  color: #fff;
  border: none;
  font-weight: 600;
  cursor: pointer;
  background-color: #e50914;
  border-radius: 4px;
}
